// Only keep styles that require ::ng-deep or can't be done with Tailwind
.column-selector {
  ::ng-deep {
    // PrimeNG multiselect customizations that can't be done with Tailwind classes
    .p-multiselect {
      min-height: 120px;

      .p-multiselect-label {
        min-height: 2.5rem;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 0.25rem;
      }

      .p-multiselect-token {
        font-size: 0.875rem;
        padding: 0.25rem 0.5rem;
      }
    }
  }
}
